<template>
   <b-modal id="myModal" title="Nuevo Equipo" 
        ref="myModal"
        body-class="mymodal-body"
        footer-class="mymodal-footer">
        <b-form v-if="show"  @submit="onSubmit"  @reset="onReset">
            <b-form-group label="Nombre:"
                description="Escribe Nombre"    
            >
           <b-form-input v-model="form.nombre"
                              required
                              placeholder="Nombre Equipo">
                </b-form-input>
            </b-form-group>
            <b-form-group label="URL" description="Escriba URL">
                  <b-form-input v-model="form.logo"
                              required
                              placeholder="Escribe URL">
                </b-form-input>
            </b-form-group>
            <b-form-group label="Jugadores" description="Seleccion Jugadores">
                <b-form-select :options="jugadores"
                                required multiple
                                v-model="form.jugadores">
                </b-form-select>
            </b-form-group>
            <b-form-group>
                <b-form-radio-group v-model="form.estado">
                    <b-form-radio value="false">
                        Inactivo
                    </b-form-radio>
                    <b-form-radio value="true">
                        Activo
                    </b-form-radio>
                </b-form-radio-group>
            </b-form-group>
            <div class="form-actions float-right">
                <b-button pill type="reset" variant="danger">Limpiar</b-button>
                <b-button pill type="submit" variant="primary">Enviar</b-button>
            </div>
        </b-form>

        <div slot="modal-footer"></div>
    </b-modal>
</template>

<script>
export default {
    data() {
        return {
            form:{
                nombre:"",
                logo: "",
                jugadores: [],
                estado: false
            },
            jugadores: [
                { text: "Elige un jugador", value: null},
                "Jugador 1",
                "Jugador 2",
                "Jugador 3",
                "Jugador 4",
                "Jugador 5",
            ],
            show: true
        }
    },
    methods:{
        onReset(evt){
            evt.preventDefault();
            
            this.form.nombre = "";
            this.form.logo = "";
            this.form.jugadores = [];
            this.form.estado = false;

            this.show = false;
            this.$nextTick(()=>{
                this.show = true
            })
        },
        onSubmit(evt) {
            evt.preventDefault();
            //alert(JSON.stringify(this.form));
            this.$emit("new_team", this.form);
            this.$refs.myModal.hide();
            
        }
    }
}
</script>

<style >
    .mymodal-body .col-form-label{
        text-align: left;
    }
    .mymodal-body .text-muted{
        text-align: right;
    }
</style>